.header-color 
{
  background: #0F2027;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}

.landing-grid 
{
  text-align: center;
  height: auto;
  width: auto;
  background: #0F2027;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.banner-text 
{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  padding-top: 3px;
  padding-bottom: 20px;


}

.banner-text h1
{
  font-size: 40px;
  font-weight: bold;
  color: white;

}
.banner-text hr 
{
  border-top: 10px solid royalblue;
  width: 50%;
  margin: auto;

}

.banner-text p
{
  text-align: center;
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-link
{
  text-align: center;
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-link i
{
  color: rgb(176, 163, 211);
  font-size: 5em;
}

.avatar-img 
{
  height: 250px;
  padding-top: 2em;
  padding-bottom: 15px;
  border-radius: 80%;
}

.project-grid 
{

}

.contact-body
{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid
{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2
{
  font-family: 'Anton', sans-serif;
}

.contact-grid p
{
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr
{
  border-top: 5px solid black;
  width: 50%;
  margin: auto;
}

.contact-list i
{
  font-size: 66px;
  padding-right: 5rem;
}